import React from 'react'
import colheres from '../../Assets/Img/colheres.jpg'
import './mainBanner.css'

export default class MainBanner extends React.Component {
    render() {
        return (
            <div className="main-banner">
                <img src={colheres} alt="colheres" />

                <div className="mask"></div>
                
                <div className="texto">
                    <h2>As Melhores Receitas</h2>
                    <p>Doces, Salgados e muito mais...</p>
                </div>
            </div>
        )
    }
}